@mixin animation($style) {
    -webkit-animation: #{$style};
    animation: #{$style};
    -moz-animation: #{$style};
    -o-animation: #{$style};
    -ms-animation: #{$style};
}

@mixin keyframes($name) {
    @keyframes #{$name} {
        @content;
    }

    @-webkit-keyframes #{$name} {
        @content;
    }

    @-moz-keyframes #{$name} {
        @content;
    }
}

.wpgen-forms {
    &__mb-1 {
        margin-bottom: 10px !important;
    }

    &__mb-2 {
        margin-bottom: 20px !important;
    }

    &__mt-2 {
        margin-top: 20px !important;
    }

    &__mr-2 {
        margin-right: 20px !important;
    }

    &__headerWrapper {
        display: flex;
        align-items: center;
        gap: 10px;
    }

    &__desc {
        font-size: 12px !important;
        font-style: italic;
        margin: 0px !important;
    }

    &__table td,
    &__table th {
        padding: 10px !important;
    }

    &__form-plugin {
        width: 100% !important;
    }

    &__generative-button {
        background: linear-gradient(
            60deg,
            #bf3a4a,
            #f37055,
            #ef4e7b,
            #a166ab,
            #5073b8,
            #1098ad,
            #07b39b,
            #6fba82
        ) !important;
        background-size: 300% 300% !important;
        color: #fff !important;
        border-radius: 5px !important;
        border-color: transparent !important;
        font-size: 16px !important;
        padding: 10px 20px !important;
        font-weight: 600 !important;
        text-decoration: none !important;
        // @include animation("animatedgradient 6s ease infinite alternate");
        // @include keyframes(animatedgradient) {
        //     0% {
        //         background-position: 0% 50%;
        //     }
        //     50% {
        //         background-position: 100% 50%;
        //     }
        //     100% {
        //         background-position: 0% 50%;
        //     }
        // }

        &--small {
            padding: 5px !important;
            font-size: 13px !important;
        }
    }

    &__cards {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
    }

    &__card {
        padding: 10px 20px;
        background: #fff;
        border: 1px solid #e0e0e0;
        border-radius: 10px;
        box-shadow: 0 0 1px rgba(0, 0, 0, 0.1);
        
        &--header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 2px;

            & h3 {
                font-size: 18px;
                background: linear-gradient(
                    60deg,
                    #bf3a4a,
                    #f37055,
                    #ef4e7b,
                    #a166ab,
                    #5073b8,
                    #1098ad,
                    #07b39b,
                    #6fba82
                );
                -webkit-background-clip: text;
                background-clip: text;
                color: transparent !important;
                display: inline-block;
                font-weight: 700;
            }
        }

        &--body {
            padding: 10px 0;
        }
    }

    &__count {
        font-size: 1.6rem;
    }

    &__text-danger{
        color: #dc3545 !important;
    }

    &__hint{
        margin-top: 5px !important;
        font-size: 14px;
        color: #6c757d;
        font-style: italic;
    }

    &__is-required{
        color: #dc3545 !important;
        font-size: 14px;
        font-weight: 600;
    }
}
